 .container {
   display: block;
   padding: 20 8%;
 }

 :global(.card) {
   border-radius: 5px;
   background-color: #ffffff;
   -webkit-box-shadow: 0 1px 2px 0 rgba(81, 96, 115, .2);
           box-shadow: 0 1px 2px 0 rgba(81, 96, 115, .2);
   border: solid 0.5px #b9b9c4;
 }

 .smCard {
   max-width: 250px;
 }

 .mdCard {
   max-width: 500px;
 }

 .btnPrimary {
   background-color: #651fff;
 }

 .btnPrimary:hover{
   opacity: 0.8;
 }

 .btnSecondary {
   background-color: #f50057;
 }

 .btnSecondary:hover{
   opacity: 0.8;
 }

 :global(.nav) {
   text-decoration: none;
   color: #161735;
   padding: 0 10;
   font-weight: bold;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
 }

 :global(.leaveModal) {
   opacity: 0.01;
   -webkit-transition: 200ms;
   transition: 200ms;
 }

 :global(.active-nav) {
   color: #2962ff;
   border-bottom: 3px solid;
 }



